<template>
  <div id="swiper">
    <div
      class="swiper_box"
      @touchstart="touchStart"
      @touchmove="touchMove"
      @touchend="touchEnd"
    >
      <slot></slot>
    </div>

    <div class="circle" v-if="slideNum > 1">
      <span
        v-for="(item, index) in slideNum"
        :key="index"
        :class="{ active: index === currentIndex-1 }"
      ></span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      slideNum: 0, //轮播图片元素个数
      swiperWidth: 0, //swiper宽度
      swiperStyle: {}, //swiper样式
      currentIndex: 1, //当前索引
      scrolling: false,
    };
  },
  mounted() {
    setTimeout(() => {
      this.handleDom();

      // 2.开启定时器
      this.startTimer()
    }, 300);
  },
  methods: {
    startTimer(){ //定时器
      this.timer = setInterval(()=>{
        this.currentIndex ++
        this.scrollingImg(-this.currentIndex * this.swiperWidth)
      },3000)
    },
    stopTimer(){
      clearInterval(this.timer)
    },
    handleDom() {
      //操作dom
      let swiperEl = document.querySelector(".swiper_box"),
        slideEls = document.getElementsByClassName("slide");
      console.log(slideEls.length);
      this.slideNum = slideEls.length;
      if (this.slideNum > 1) {
        //为了实现无缝轮播
        let cloneFirst = slideEls[0].cloneNode(true), //克隆第一张
          cloneLast = slideEls[this.slideNum - 1].cloneNode(true); //克隆最后一张
        swiperEl.insertBefore(cloneLast, slideEls[0]);
        swiperEl.appendChild(cloneFirst);
        this.swiperWidth = swiperEl.getBoundingClientRect().width;
        this.swiperStyle = swiperEl.style;
      }

      console.log(this.swiperWidth);
      this.setTransform(-this.swiperWidth);
    },
    checkPositon() {
      //实现无缝轮播
      window.setTimeout(() => {
        this.swiperStyle.transition = "0ms";
        if (this.currentIndex >= this.slideNum + 1) {
          //如果走到克隆后的最后一张图片了
          this.currentIndex = 1; //需要无动画瞬间拉回第一张； 其实是第二张，因为把最后一张克隆到第一张位置了
          this.setTransform(-this.currentIndex * this.swiperWidth);
        } else if (this.currentIndex <= 0) {
          //如果走图片克隆的做后一张，其实就是往后走
          this.currentIndex = this.slideNum; //需要无动画瞬间拉回最后一张； 其实是倒数第二张 因为把一张克隆到最后的位置了
          this.setTransform(-this.currentIndex * this.swiperWidth);
        }
      }, 300);
    },

    setTransform(position) {
      //设置滚动位置
      this.swiperStyle.transform = `translate3d(${position}px,0,0)`;
    },

    scrollingImg(currentPosition) {
      //滚动动画
      this.scrolling = true; //开始滚动
      this.swiperStyle.transition = "transform 300ms";
      this.setTransform(currentPosition);
      this.checkPositon(); //检查滚动的位置，实现无缝轮播
      this.scrolling = false; //滚动接收
    },

    touchStart(e) {
      //手指按下事件
      this.stopTimer() //停止定时器
      if (this.scrolling) return; //如果滚动中，禁止滚动
      this.startX = e.touches[0].pageX; //手指按下的坐标
     
      // console.log(this.startLeft);
    },
    touchMove(e) {
      //手指拖动事件
      //计算出用户拖动的距离
      this.currentX = e.touches[0].pageX;
      this.distance = this.currentX - this.startX;
      // console.log('拖动距离：'+this.distance)
      let currentPosition = -this.currentIndex * this.swiperWidth;
      let moveDistance = this.distance + currentPosition; //拖动的距离加上已经走的距离
      // console.log(moveDistance,currentPosition)
      this.setTransform(moveDistance);
    },
    touchEnd(e) {
      //手指抬起事件
      let currentMove = Math.abs(this.distance), //获取移动的距离
          backWidth = this.swiperWidth / 5; //拉力回弹条件
      if (this.distance === 0) {
        return;
      } else if (currentMove > backWidth && this.distance > 0) {
        //条件成立，需要切换图片
        this.currentIndex--;
      } else if (currentMove > backWidth && this.distance < 0) {
        this.currentIndex++;
      }
      this.scrollingImg(-this.currentIndex * this.swiperWidth); //滚动图片
      this.startTimer()
    },
  },
};
</script>

<style scoped>
#swiper {
  overflow: hidden;
  position: relative;
}
.swiper_box {
  display: flex;
}
.circle {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 5px;
}
.circle span {
  display: inline-block;
  width: 8px;
  height: 8px;
  background: #fff;
  border-radius: 50%;
  margin-right: 8px;
}
.circle span.active {
  background:#f40;
}
</style>
