import { debounce } from "./index"

export const itemListenMixin = {
    data() {
        return {
            itemImgLoad: null
        }
    },
    mounted: function() {
        const self = this;
        let refresh = debounce(this.$refs.scroll.refresh, 100); //调用防抖函数处理
        this.getModuleOffSetTop = debounce(() => {
            if (this.moduleOffSetTop) {
                this.moduleOffSetTop = []
                this.moduleOffSetTop.push(0);
                this.moduleOffSetTop.push(self.$refs.goodsParams.$el.offsetTop);
                this.moduleOffSetTop.push(self.$refs.goodsRate.$el.offsetTop);
                this.moduleOffSetTop.push(self.$refs.goodsRecommend.$el.offsetTop);
                console.log(this.moduleOffSetTop);
            }
        }, 50)
        this.itemImgLoad = () => {
            refresh()
            this.getModuleOffSetTop()
        }
        this.$bus && this.$bus.$on('imgLoad', this.itemImgLoad)
    }
}